import { useEffect, useState } from 'react';
import { ExtendedVehicleIssue } from '@/src/lib/vehicleIssues/vehicleIssueTypes';
import { db } from '@/src/firebaseConfig';
import {
  collection,
  onSnapshot,
  query,
  where,
  doc,
  updateDoc,
} from 'firebase/firestore';

interface Options {
  role?: string;
  companyId?: string;
}

export function useVehicleIssues(options?: Options) {
  const [issues, setIssues] = useState<ExtendedVehicleIssue[]>([]);
  const [loading, setLoading] = useState(true);

  const normalizedRole = options?.role?.toLowerCase();
  const isAdmin = normalizedRole === 'admin';
  const companyId = options?.companyId;

  useEffect(() => {
    // üö® N·∫øu kh√¥ng ph·∫£i admin m√† kh√¥ng c√≥ companyId ‚Üí Kh√¥ng query
    if (!isAdmin && !companyId) {
      setIssues([]);
      setLoading(false);
      return;
    }

    // ‚úÖ X√°c ƒë·ªãnh query
    const q = isAdmin
      ? query(collection(db, 'vehicleIssues')) // Admin ‚Üí get all
      : query(collection(db, 'vehicleIssues'), where('companyId', '==', companyId!)); // Company ‚Üí get by companyId

    const unsubscribe = onSnapshot(
      q,
      (snapshot) => {
        const updatedIssues: ExtendedVehicleIssue[] = snapshot.docs.map((doc) => ({
          ...(doc.data() as ExtendedVehicleIssue),
          id: doc.id,
        }));
        setIssues(updatedIssues);
        setLoading(false);
      },
      (error) => {
        console.error('Realtime fetch vehicle issues error:', error);
        setLoading(false);
      }
    );

    return () => unsubscribe();
  }, [isAdmin, companyId]);

  const handleUpdate = async (id: string, data: Partial<ExtendedVehicleIssue>) => {
    try {
      const issueRef = doc(db, 'vehicleIssues', id);
      await updateDoc(issueRef, data);
    } catch (error) {
      console.error('Failed to update issue:', error);
    }
  };

  return {
    issues,
    loading,
    handleUpdate,
  };
}
